<template>
  <Loading class="loading" v-if="loading" />
  <b-container v-else class="categories">
    <b-row class="justify-content-center">
        <b-col sm="12" md="5" v-for="item in categories" :key="item._id" @click="navigate(item._id)" class="category-item" >
            <h2>{{item.name}}</h2>
            <img v-bind:src="item.image" />
        </b-col>
    </b-row>
  </b-container>
</template>

<script>
import axios from 'axios';
import {url, showError} from '@/config/global';
import Loading from "@/components/Loading";
export default {
  name: 'Categories',
  components: {
    Loading
  },
  data: function () {
        return {
            categories: [],
            loading: 0
        }
  },
  methods: {
      navigate(id){
        this.$router.push(`/category/${id}`);
      },
      loadCategories() {
          this.loading = 1
          axios.get(`${url}/category`)
          .then(res => {
            this.categories = res.data
            this.loading = 0
          }).catch(showError)
      }
  },
  beforeMount () {
      this.loadCategories()
  }
}
</script>

<style>
.categories{
  padding: 30px;
}
.categories .category-item {
  margin: 5px 15px;
  box-shadow: 5px 5px 17px -5px rgba(0,0,0,0.7);
  display: flex;
  align-items: center;
  text-align: center;
  flex-direction: column;
  justify-content: center;
  padding: 10px;
  border-radius: 5px;
}

.categories .category-item:hover{
  cursor: pointer;
  background-color: rgba(0,0,0,0.3);
}

@media screen and (max-width: 800px) {

    .categories .category-item {
      margin: 5px 0;
    }
    .categories .category-item img{
      width: 80%;
    }
    .categories .category-item h2{
      font-size: 1.0rem;
    }
    .categories .category-item {
      margin: 10px 15px;
    }

}
</style>
