<template>       
  <div class="admin-email">
    <div class="admin-email-list">
      <Loading v-if="loading" />
      <b-table v-else striped hover :items="items" :fields="fields">
      </b-table>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import {url, showError} from '@/config/global';
import Loading from "@/components/Loading";
export default {
  name: 'AdminEmail',
  components: {
    Loading
  },
  data: function () {
        return {
            loading: 0,
            fields: ["name","email"],
            items: [],
        }
  },
  methods: {
      loadEmails() {
          this.loading = 1
          axios.get(`${url}/subscribers`)
              .then(res => {
                this.items = res.data
                this.loading = 0
                console.log(res.data)
              })
              .catch(showError)
      }
  },
  mounted() {
      this.loadEmails()
  }
}
</script>

