<template>
  <Loading v-if="loading" />
  <div class="data-category" v-else>
      <div class="data-category-title">
        <span>
          <h2>{{data.name}}</h2>
          <h3>{{data.title}}</h3>
        </span>
      </div>

      <h3 v-if="data.children.length == 0">Categoria sem publicações :(</h3>
      <div class="data-category-pub" v-for="(cont,index) in data.children" @click="navigate(cont._id)" :key="index">
        <div>
          <span>{{cont.title}}</span>
          <span>{{cont.subtitle}}</span>
        </div>
        <img :src="cont.image">
      </div>
  </div>
</template>

<script>
import axios from 'axios';
import {url, showError} from '@/config/global';
import Loading from "@/components/Loading";
export default {
name: 'Title',
  components: {
      Loading
  },
  props: {
    id: String
  },
  data: function(){
    return {
      loading: false,
      data: null
    }
  },
  methods: {
    navigate(id){
      this.$router.push(`/publication/${id}`);
    },
    loadCategory(){
      this.loading = 1
          axios.get(`${url}/category/${this.id}`)
          .then(res => {
            this.data = res.data[0]
            this.loading = 0
          }).catch(showError)
    }
  },
  beforeMount () {
      this.loadCategory()
  }
}
</script>

