<template>
  <div class="new-subscriber">
    <h3>Cadastre-se para receber<br>nossas postagens!</h3>
    <b-form-group label="Nome:" label-for="new-name">
        <b-form-input id="new-name" type="text" v-model="new_subscriber.name" required placeholder="Insira seu nome"></b-form-input>
    </b-form-group>
     <b-form-group label="Email:" label-for="new-email">
        <b-form-input id="new-email" type="text" v-model="new_subscriber.email" required placeholder="Insira seu email"></b-form-input>
    </b-form-group>
    <div class="buttons">
        <b-button class="mr-5" variant="danger" @click="reset">Cancelar</b-button>
        <b-button variant="dark" @click="create">Cadastrar</b-button>  
    </div>
        
  </div>
</template>

<script>
import axios from 'axios';
import {url, showError} from '@/config/global';
export default {
name: 'Title',
  data: function(){
    return {
      new_subscriber: {
        name: null,
        email: null
      }
    }
  },
  methods: {
    reset(){
      this.$router.push('/');
    },
    create(){
      axios.post(`${url}/subscribers`,this.new_subscriber)
        .then(() => {
          this.$toasted.global.defaultSuccess({msg:"Cadastrado!"})
          this.$router.push('/');
        }).catch(showError)
    }
  }
}
</script>

<style>
.new-subscriber {
  display: flex;
  padding: 30px;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
}

.new-subscriber .buttons {
  display: flex;
  justify-content: space-between;
}

.new-subscriber h3 {
  text-align: center;
}

@media screen and (max-width: 800px) {
  .new-subscriber {
    padding: 10px;
  }
}
</style>