<script>
import axios from 'axios';
import {url, showError} from '@/config/global';
import Loading from "@/components/Loading";
export default {
  name: 'Home',
  components: {
    Loading
  },
  data: function(){
    return {
      data: [],
      loading: 0
    }
  },
  methods: {
      navigate(id){
        this.$router.push(`/publication/${id}`);
      },
      loadPublications() {
          this.loading = 1
          axios.get(`${url}/publication-favorites`)
          .then(res => {
            this.data = res.data
            this.loading = 0
          }).catch(showError)
      }
  },
  beforeMount () {
      this.loadPublications()
  }
}
</script>

